.card {
  min-width: 230px;
  height: 100%;
  border-radius: 10px;
  transition: all 0.2s;
  box-shadow: 0 0 5px rgba(0,0,0,0.3);
  background-color: var(--dark-clr);
  transition: 0.3s all;
  overflow: hidden;

}
.cardTextWrap{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 60px;
  transition: all 0.3s;
}

.cardTextWrap button{
  width: 100%;
  height: 100%;
  border: none;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  font-weight: 600;
  font-size: 1.3rem;

}

.badge {
  top: 10px;
  left: 20px;
