}
.cardName{
  transition: 0.3s all;

 margin-top: 5px;
 background-color: var(--dark-clr);
 color: #fff;
}

.card:hover {
  -webkit-transform: scale(1.0125);
          transform: scale(1.0125);
  box-shadow: 0 0 7px rgba(0,0,0,0.8);
}
.card:hover .card-inner{
  display: inline-block;
}

.card:hover .cardName{
  display: none;
}

.card-inner{
  display: none;
}

.content { 
